import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: '/users',
  },
  {
    path: '/users',
    component: () => import('@/views/Users.vue'),
    meta: {
      layout: 'main',
    },
  },
  {
    path: '/users/:id',
    name: 'users',
    component: () => import('@/views/User.vue'),
    meta: {
      layout: 'main',
    },
  },
  {
    path: '/admins',
    component: () => import('@/views/Admins.vue'),
    meta: {
      layout: 'main',
    },
  },
  {
    path: '/withdraws',
    component: () => import('@/views/Withdraws.vue'),
    meta: {
      layout: 'main',
    },
  },
  {
    path: '/games',
    component: () => import('@/views/Games.vue'),
    meta: {
      layout: 'main',
    },
  },
  {
    path: '/promocodes',
    component: () => import('@/views/Promocodes.vue'),
    meta: {
      layout: 'main',
    },
  },
  {
    path: '/chat',
    component: () => import('@/views/Chat.vue'),
    meta: {
      layout: 'main',
    },
  },
  {
    path: '/dashboard',
    component: () => import('@/views/Dashboard.vue'),
    meta: {
      layout: 'main',
    },
  },
  {
    path: '/auth',
    component: () => import('@/views/Auth.vue'),
    meta: {
      layout: 'empty',
    },
  },
  {
    path: '/logout',
    meta: {
      layout: 'empty',
    },
    beforeEnter: (to, from, next) => {
      next('/auth');
    },
  },
  {
    path: '*',
    redirect: '/',
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
